<template>
  <v-container grid-list-lg>
    <v-layout row wrap>
      <v-flex xs12>
        <v-card>
          <v-card-title primary-title>
            <v-container>
              <h3 class="headline mb-0">Añadir nuevo objetivo</h3>
              <v-layout row justify-space-between>
                <v-flex sm6>
                  <v-text-field v-model="newObjetive.description" label="El dia de hoy haré..."></v-text-field>
                </v-flex>
                <v-flex sm6>
                  <v-text-field v-model="newObjetive.timeLimit" label="para antes de..."></v-text-field>
                </v-flex>
              </v-layout>
              <v-btn round color="primary" @click="addObjetive" dark>Añadir objetivo</v-btn>
            </v-container>
          </v-card-title>
        </v-card>
      </v-flex>
      <v-flex xs12>
        <v-toolbar flat color="white">
          <v-toolbar-title>Objetivos del día de hoy</v-toolbar-title>
          <v-divider class="mx-2" inset vertical></v-divider>
        </v-toolbar>
        <v-data-table expand :headers="headers" :items="goals" class="elevation-1" hide-actions>
          <template v-slot:items="props">
            <td class="text-xs-left">
              <v-switch color="red" v-model="props.item.state"></v-switch>
            </td>
            <td class="text-xs-left">{{ props.item.description }}</td>
            <td class="text-xs-left">{{ props.item.timeLimit }}</td>
            <td class="justify-center layout px-0">
              <v-icon small class="mr-2" @click="editItem(props.item)">edit</v-icon>
              <v-icon small @click="deleteObjetive(props.item)">delete</v-icon>
            </td>
          </template>
        </v-data-table>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { customCopyObject } from "../../tools/customCopyObject";
import axios from "axios";
export default {
  data() {
    return {
      goals: [],
      newObjetive: {
        state: false,
        description: "",
        timeLimit: ""
      },
      headers: [
        {
          text: "¿Lo hiciste?",
          align: "left",
          sortable: false,
          value: "state"
        },
        { text: "Descripción", value: "description", align: "left" },
        { text: "Plazo", value: "timeLimit", align: "left" },
        { text: "Acciones", value: "name", sortable: false, align: "center" }
      ]
    };
  },
  mounted() {
    this.getObjetivesAPI();
  },
  methods: {
    getObjetivesAPI() {
      axios
        .get("/api/goals")
        .then(response => {
          this.goals = response.data.goals;
        })
        .catch(error => {
          // handle error
          console.log(error);
        });
    },
    addObjetive() {
      axios
        .post("/api/goals", {
          state: this.newObjetive.state,
          description: this.newObjetive.description,
          timeLimit: this.newObjetive.timeLimit
        })
        .then(response => {
          this.goals.push(customCopyObject(this.newObjetive));
        })
        .catch(error => {
          // handle error
          console.log(error);
        });
    },
    deleteObjetive(item) {
      const index = this.goals.indexOf(item);
      confirm("Are you sure you want to delete this item?") &&
        this.goals.splice(index, 1);
    }
  }
};
</script>

